/*style*/
html{
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  font-size: 20px;
}
html,body {
  -webkit-touch-callout:none;
  -webkit-text-size-adjust:none;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling:auto;
  -webkit-user-select:auto;
  width: 100%;
}
body {
  line-height: 1.5;
  font-size: 0.8rem;
  color: #1c1c1c;
  background-color: #f5f5f5;
  outline: 0;
}
html,body,header,section,footer,div,ul,ol,li,img,a,span,em,del,legend,center,strong,var,fieldset,form,label,dl,dt,dd,cite,input,hr,time,mark,code,figcaption,figure,textarea,h1,h2,h3,h4,h5,h6,p{
  margin:0;
  border:0;
  padding:0;
  font-style:normal;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  outline: none;

}
@media only screen and (min-width: 400px) {
  html {
    font-size: 21.33333333px !important;
  }
}
@media only screen and (min-width: 414px) {
  html {
    font-size: 21px !important;
  }
}
@media only screen and (min-width: 480px) {
  html {
    font-size: 25.6px !important;
  }
}
ul, li {
  list-style: none;
}
p
{
  font-size: 0.7rem;
  color: #757575;
}
a
{
  color: #0062cc;
  text-decoration: none;
  background-color: transparent;
}
textarea
{
  overflow: auto;
  resize: none;
}
button
{
  overflow: visible;
}
button,select
{
  text-transform: none;
}
button,input,select,textarea
{
  font: inherit;
  color: inherit;
}
body,html
{
  width: 100%;
  height: 100%;
  overflow:hidden;
}
.cui-content
{
  width:100%;
  height:100%;
  position:relative;
  /*overflow-y:auto;*/
  overflow-x:hidden;
  -webkit-overflow-scrolling:touch;
}
/*一些公用的css*/
.cui-flex-wrap
{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  min-height:0;
}
.cui-flex-con
{
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  overflow:hidden;
  position:relative;
  min-height:0;
}
.cui-flex-vertical
{
  -webkit-box-orient: vertical;
  -webkit-flex-flow: column;
  flex-flow: column;
}
/*一些公用的css end*/
/*公共css*/
.cui-clearfix
{
  clear: both;
}
.cui-clearfix:before
{
  display: table;
  content: " ";
}
.cui-clearfix:after
{
  clear: both;
}
/*文本对齐*/
.cui-text-center
{
  text-align: center;
}
.cui-text-left
{
  text-align: left;
}
.cui-text-right
{
  text-align: right;
}

/*fixed定位*/
.cui-fixed-top
{
  position:fixed !important;
  top:0 !important;
}

.cui-fixed-bottom
{
  position:fixed !important;
  bottom:0 !important;
}
.cui-fixed-left
{
  position:fixed !important;
  left:0 !important;
}
.cui-fixed-right
{
  position:fixed !important;
  right:0 !important;
}
