/*图标end*/
/*头部*/
.cui-header
{
  min-height:2rem;
  line-height: 2rem;
  width:100%;
  background-color:white;
  padding:0 .4rem;
  position:relative;
  z-index:3;
}

.cui-heade-margin
{
  margin-top:2rem;
}
.cui-header .cui-header-title
{
  display:inline-block;
  width:70%;
  text-align: center;
  position:absolute;
  padding-top:inherit;
  top:0;
  left:0;
  height:100%;
  line-height: 2rem;
  z-index:0;
  overflow:hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  right:0;
  margin:auto;
}
/*处理刘海*/
.cui-header-bangs
{
  padding-top:20px;
}

.cui-header-bangs+.cui-heade-margin
{
  margin-top:2rem;
  -webkit-transform: translateY(20px);
  -moz-transform: translateY(20px);
  -ms-transform: translateY(20px);
  -o-transform: translateY(20px);
  transform: translateY(20px);
}

.cui-header .cui-iconfont
{
  position:relative;
  z-index:1;
}
.cui-header .cui-iconfont:before
{
  font-size:1.2rem !important;
}
.cui-header .cui-header-btn
{
  float:right;
  margin-left:.2rem;
}
.cui-header .cui-header-search
{
  height:100%;
  padding:0 .4rem;
  top:0;
}

.cui-header .cui-header-enter
{
  display:inline-block;
  float:right;
  font-size:16px;
  position:relative;
  z-index:2;
}

.cui-header-primary
{
  background-color:#00bcd4;
  color:white;
}

.cui-header-danger
{
  background-color: #cc1227;
  color:white;
}
.cui-header-warning
{
  background-color: #ffc059;
  color:white;
}

.cui-header-sky
{
  background-color: deepskyblue;
  color:white;
}

.cui-header-dark
{
  background-color:#354C54;
  color:white;
}


/*头部end*/

/*搜索框*/
.cui-searchbar
{
  min-width:100px;
  height:2rem;
  position:relative;
}
.cui-searchbar input
{
  width:100%;
  height:1.4rem;
  background-color:#f5f5f5;
  -webkit-user-select:text;
  border-radius:1rem;
  padding:0 .75rem;
  line-height: 1.4rem;
  color:#1c1c1c;
  margin-top:.3rem;
}
.cui-searchbar .cui-searchbar-btn
{
  display:inline-block;
  height:1.2rem;
  width:1.2rem;
  border-radius:50%;
  background-color: rgb(224, 224, 224);
  position:absolute;
  right:.15rem;
  top:.4rem;
  text-align: center;
  line-height: 1.2rem;
}
.cui-searchbar-btn:before
{
  content:"\e741";
  font-family:"cui-iconfont";
  vertical-align: top;
  color:white;
}
/*搜索框end*/

/*按钮*/
.cui-btn
{
  display:inline-block;
  padding:.4rem 1rem;
  line-height: 1;
  background-color: #fcfcfc;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
  cursor:pointer;
  white-space:nowrap;
}
.cui-btn-sm
{
  padding:.2rem .4rem;
  font-size:.7rem;
}
.cui-btn-radius:not(.cui-btn-sm)
{
  border-radius:.9rem;
  height:1.8rem;
}
.cui-btn-radius.cui-btn-sm
{
  border-radius:.6rem;
  height:1.2rem;
}
.cui-btn-block
{
  display:block;
  text-align: center;
}
.cui-btn-default
{
  background-color:#fcfcfc;
  border:1px solid #dedede;
}
.cui-btn-default.cui-line
{
  border:1px solid #1c1c1c;
}
.cui-btn-primary
{
  background-color:#478CD1;
  border:1px solid #478CD1;
  color:white;
}
.cui-btn-primary.cui-line
{
  border:1px solid #478CD1;
  color:#478CD1;
}
.cui-btn-danger
{
  background-color:#EE3E55;
  border:1px solid #EE3E55;
  color:white;
}
.cui-btn-danger.cui-line
{
  border:1px solid #EE3E55;
  color:#EE3E55;
}
.cui-btn-warning
{
  background-color:#EDCC42;
  border:1px solid #EDCC42;
  color:white;
}
.cui-btn-warning.cui-line
{
  border:1px solid #EDCC42;
  color:#EDCC42;
}
.cui-btn-success
{
  background-color:#82C35C;
  border:1px solid #82C35C;
  color:white;
}
.cui-btn-success.cui-line
{
  border:1px solid #82C35C;
  color:#82C35C;
}
.cui-btn-dark
{
  background-color:#354C54;
  border:1px solid #354C54;
  color:white;
}
.cui-btn-dark.cui-line
{
  border:1px solid #354C54;
  color:#354C54;
}
.cui-btn-purple
{
  background-color:#CB56AF;
  border:1px solid #CB56AF;
  color:white;
}
.cui-btn-purple.cui-line
{
  border:1px solid #CB56AF;
  color:#CB56AF;
}

.cui-line
{
  background-color:transparent;
}
/*按钮end*/

/*表单*/
.cui-form
{
  display:block;
  width:100%;
  background-color:white;
  padding:.4rem;
}

.cui-input
{
  display:block;
  color:inherit !important;
  min-height:2rem;
  margin-bottom:.6rem;
  height:2rem;
  position:relative;
}

.cui-input-link:after
{
  font-family:"cui-iconfont";
  content:"\e6ff";
  display:inline-block;
  height:2rem;
  width:20px;
  color:#808080;
  line-height: 2rem;
  text-align: center;
  position:absolute;
  right:0;
  bottom:0;
}
.cui-input input:focus,.cui-input textarea:focus
{
  border-bottom:1px solid deepskyblue;
}
.cui-input label,.cui-input .label
{
  display:inline-block;
  width:5rem;
  height:2rem;
  line-height: 2rem;
  overflow:hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  white-space: nowrap;
  position:relative;
  z-index: 2;
}
.cui-input input,.cui-input select
{
  line-height: 2rem;
  color:black;
  width:100%;
  height:100%;
  margin:auto;
  -webkit-user-select:text;
  position:absolute;
  top:0;
  left:0;
  padding-left:5rem;
  border-bottom:1px solid #E6E6E6;
  -webkit-transition: border-bottom .3s;
  -moz-transition: border-bottom .3s;
  -ms-transition: border-bottom .3s;
  -o-transition: border-bottom .3s;
  transition: border-bottom .3s;
}
.cui-input textarea
{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  line-height: normal;
  height:5rem;
  margin-top:8px;
  -webkit-user-select:text;
  padding-left:5rem;
  border-bottom:1px solid #e2e2e2;
  -webkit-transition: border-bottom .3s;
  -moz-transition: border-bottom .3s;
  -ms-transition: border-bottom .3s;
  -o-transition: border-bottom .3s;
  transition: border-bottom .3s;
}
.cui-input-btn .cui-btn
{
  position:absolute;
  margin:auto;
  right:0;
  top:0;
  bottom:0;
}
.cui-textarea
{
  height:5rem;
}

/*表单end*/

/*下拉刷新插件*/
.cui-refresh-content
{
  height:100%;
  width:100%;
  position:relative;
  background-color: #f9f9f9;
  -webkit-overflow-scrolling:touch;
}
.cui-refresh-content .cui-refresh-header
{
  position:absolute;
  width:100%;
  text-align: center;
}
.cui-refresh-header span
{
  font-size:.6rem;
  display:inline-block;
  width:3rem;
}
.cui-refresh-header img
{
  height:100%;
  vertical-align: middle;
  margin-right:5px;
}
.cui-refresh-wrap
{
  min-height:100%;
}
/*下拉刷新插件end*/


/*加载插件*/
.cui-loading-content
{
  height:1rem;
  text-align: center;
  font-size:14px;
}
.cui-loading-content img
{
  height:100%;
  margin-right:5px;
}

/*轮播图*/
.cui-swiper-content
{
  width:100%;
  height:10rem;
  background-color:white;
  white-space: nowrap;
  position:relative;
  overflow:hidden;
  -webkit-overflow-scrolling:auto;
}
.cui-swiper-content .cui-swiper-wrap
{
  white-space: inherit;
  margin-left:0;
  width:100%;
  font-size:0;
  height:100%
  /*-webkit-transition: margin-left .5s;
  -moz-transition: margin-left .5s;
  -ms-transition: margin-left .5s;
  -o-transition: margin-left .5s;
  transition: margin-left .5s;*/

}
.cui-swiper-content .cui-swiper-slide
{
  width:100%;
  height:100%;
  display:inline-block;
  vertical-align: top;

  text-align: center;
  font-size:1rem;
  background-position:center;
  -webkit-background-size: cover;
  background-size: cover;
  background-repeat:no-repeat;
}
.cui-swiper-content .cui-swiper-pagination
{
  position:absolute;
  width:100%;
  height:3rem;
  line-height: 3rem;
  bottom:0;
  left:0;
  text-align: center;
}
.cui-swiper-pagination span
{
  display:inline-block;
  width:.5rem;
  height:.5rem;
  margin:4px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  background-color:white;
  -webkit-transition: all .2s;
  -moz-transition: all .2s;
  -ms-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}
.cui-swiper-content-v .cui-swiper-pagination
{
  position:absolute;
  width:1rem;
  line-height: normal;
  min-height:100px;
  margin:auto;
  left:auto;
  top:0;
  right:2rem;
  bottom:0;
}
.cui-swiper-pagination .cui-swiper-pagination-acitve
{
  background-color:firebrick;
}
.cui-swiper-content-v
{
  white-space:normal !important;
}
/*轮播图end*/


/*popModal插件*/
.cui-popmodal
{
  position:absolute;
  width:150px;
  z-index:4;
  opacity:1;
  -webkit-transform: translate(0,0);
  -moz-transform: translate(0,0);
  -ms-transform: translate(0,0);
  -o-transform: translate(0,0);
  transform: translate(0,0);
  -webkit-transition: all .2s;
  -moz-transition: all .2s;
