}
.cui-tree-item-dot-warning .cui-iconfont
{
  color:#fdc07c !important;
}
.cui-tree-item-dot-danger .cui-iconfont
{
  color:#FF1744 !important;
}
.cui-tree .cui-iconfont:before
{
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0;
  text-align: center;
  line-height: 1rem;
}
/*时间树（带序号）*/
.cui-tree .cui-tree-item-num
{
  font-size:.6rem;
  width:.8rem;
  height:.8rem;
  position:absolute;
  top:0;
  left:-0.2rem;
  background-color:#e6e6e6;
  z-index:2;
  border-radius:50%;
  color:grey;
  line-height: .8rem;
}

.cui-tree-item-dot-active .cui-tree-item-num
{
  color:white;
  background-color:#81C784;
}
.cui-tree-item-dot-warning .cui-tree-item-num
{
  color:white;
  background-color:#fdc07c;
}
.cui-tree-item-dot-danger .cui-tree-item-num
{
  color:white;
  background-color:#FF1744;
}

/*时间树（横）*/
.cui-tree-h
{
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
}
.cui-tree-h .cui-tree-item
{
  -webkit-box-flex:1;
  -webkit-flex:1;
  flex:1;
}
.cui-tree-h .cui-tree-item .cui-tree-icon
{
  width:100%;
  height:.4rem;
}
.cui-tree-h .cui-tree-item .cui-tree-icon:before
{
  width:100%;
  height:.1rem;
  position:absolute;
  top:0;
  right:0;
  left:50%;
  bottom:0;
  margin:auto;
}
.cui-tree-h .cui-tree-item .cui-tree-icon:after
{
  top:0;
  right:0;
  left:0;
  bottom:0;
  margin:auto;
}
.cui-tree-h .cui-tree-item .cui-tree-detail
{
  padding:1rem 1rem .5rem 1rem;
  text-align: center;
}
.cui-tree-h .cui-iconfont,.cui-tree-h .cui-tree-item-num
{
  top:0;
  right:0;
  bottom:0;
  left:0;
  margin:auto;
}
/*dialog弹窗*/
.cui-dialog
{
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0;
  z-index:2;
  overflow:hidden;
}
.cui-dialog .cui-dialog-shelter
{
  opacity:0;
  -webkit-transition: opacity .6s;
  -moz-transition: opacity .6s;
  -ms-transition: opacity .6s;
  -o-transition: opacity .6s;
  transition: opacity .6s;
}


.cui-dialog-toast
{
  position:fixed;
  top:90%;
  left:50%;
  opacity:0;
  -webkit-transition: all .3s;
  -moz-transition: all .3s;
  -ms-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  padding:.1rem .3rem;
  background-color: rgba(51, 51, 51, 0.58);
  color:white;
  min-width:2rem;
  font-size:12px;
  border-radius:12px;
}
.cui-dialog-toast.cui-dialog-active
{
  opacity:1;
  top:88%;
}

.cui-dialog-anycall .cui-dialog-title,.cui-dialog-anycall .cui-dialog-detail
{
  text-align: left !important;
}
.cui-dialog-anycall .cui-dialog-detail
{
  margin-top:.5rem;
}
.cui-dialog-anycall .cui-dialog-btn
{
  display:block !important;
  padding-top:1rem;
  text-align: right;
}
.cui-dialog-anycall .cui-dialog-btn .cui-dialog-btn-item
{
  display:inline;
  height:auto;
  color:deepskyblue;
  margin-left:20px;
  flex:none;
  -webkit-flex: none;
}


.cui-dialog .cui-dialog-shelter
{
  width:100%;
  height:100%;
  background-color: rgba(0, 0, 0, 0.2);
}
.cui-dialog .cui-dialog-content
{
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform-origin:50% 50%;
  -moz-transform-origin:50% 50%;
  -ms-transform-origin:50% 50%;
  -o-transform-origin:50% 50%;
  transform-origin:50% 50%;
  -webkit-transform:translate(-50%,-50%) scale(1.5);
  -moz-transform:translate(-50%,-50%) scale(1.5);
  -ms-transform:translate(-50%,-50%) scale(1.5);
  -o-transform:translate(-50%,-50%) scale(1.5);
  transform:translate(-50%,-50%) scale(1.5);

  width:80%;
  min-height:4rem;
  padding:.5rem;
  background-color:white;
  border-radius:.5rem;
  opacity:0;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -ms-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.cui-dialog.cui-dialog-active .cui-dialog-shelter
{
  opacity:1;
}
.cui-dialog.cui-dialog-active .cui-dialog-content
{
  opacity:1;
  -webkit-transform-origin:50%;
  -moz-transform-origin:50%;
  -ms-transform-origin:50%;
  -o-transform-origin:50%;
  transform-origin:50%;
  -webkit-transform:scale(1) translate(-50%,-50%);
  -moz-transform:scale(1) translate(-50%,-50%);
  -ms-transform:scale(1) translate(-50%,-50%);
  -o-transform:scale(1) translate(-50%,-50%);
  transform:scale(1) translate(-50%,-50%);

}
.cui-dialog-content .cui-dialog-detail
{
  text-align: center;
  font-size:14px;
}
.cui-dialog-detail input
{
  width:100%;
  border-bottom:1px solid lightgray;
  padding:.5rem 0;
  -webkit-transition: border-bottom .3s;
  -moz-transition: border-bottom .3s;
  -ms-transition: border-bottom .3s;
  -o-transition: border-bottom .3s;
  transition: border-bottom .3s;
}
.cui-dialog-detail input:focus
{
  border-bottom:1px solid #41c7ff;
}
.cui-dialog-content .cui-dialog-title
{
  font-size:18px;
  text-align: center;
}
.cui-dialog-content .cui-dialog-btn
{
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
}
.cui-dialog-btn .cui-dialog-btn-item
{
  height:1.5rem;
  color:deepskyblue;
  text-align: center;
  margin-top:.5rem;
  line-height: 1.5rem;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex:1;
}

/*列表*/
.cui-list
{
  margin-bottom:.5rem;
  background-color:white;
  font-family:微软雅黑;
}
.cui-list .cui-list-item
{
  min-height:2rem;
  display:-webkit-box;
  display:-webkit-flex;
  display:flex;
  align-items: center;
  word-break: break-all;
}
.cui-list-item
{
  border-bottom:1px solid #f3f2f2;
}
.cui-list-item:last-child
{
  border-bottom:none;
}
.cui-list-item .cui-list-icon
{
  height:2.5rem;
  width:2.5rem;
  background-color:gray;
  margin:.5rem;
  overflow:hidden;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.cui-list-icon img
{
  width:100%;
}
.cui-list-item .cui-list-center
{
  -webkit-box-flex: 1;
  -webkit-flex:1;
  flex:1;
  height:2.3rem;
  overflow:hidden;
  font-weight: bold;
  color:#4e4a4a;
}
.cui-list-title
{
  font-size:.8rem;
  height:1.3rem;
  font-weight:bold;
  width:100%;
  white-space: normal !important;
  line-height: 1.3rem;
  text-overflow:ellipsis;
  overflow:hidden;
}
.cui-list-other
{
  font-size:.6rem;
  color:gray;
  width:100%;
  height:1rem;
  line-height: 1rem;
  overflow:hidden;
  font-weight: normal;
}
.cui-list-item .cui-list-right
{
  height:2.3rem;
  width:2.5rem;
  margin:.5rem;
}
.cui-list-link .cui-list-right
{
  background-position:center right;
  background-repeat: no-repeat;
  background-size:.8rem;
  background-image:url(data:image/png;base64,)
}

.cui-list-right-top,.cui-list-right-bottom
{
  font-size:.6rem;
  text-align: right;
  color:gray;
}
.cui-list-right-top
{
  height:1.3rem;
}

.cui-list-sm .cui-list-icon,.cui-list-sm .cui-list-right
{
  height:1.5rem;
  width:1.5rem;
}
.cui-list-sm .cui-list-center
{
  height:1.3rem;
  line-height: 1.3rem;
}

.cui-list-setting .cui-list-item
{
  margin-left:.5rem;
}
.cui-list-setting .cui-list-icon,.cui-list-setting .cui-list-right
{
  height:1.3rem;
  width:1.3rem;
}
.cui-list-setting .cui-list-center
{
  height:1.1rem;
  line-height: 1.1rem;

}



